/* CSS for the scroll-to-top button */
.scroll-to-top-button {
  position: fixed;
  bottom: 20px;
  right: 20px;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #0E4165;
  color: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.scroll-to-top-button.visible {
  opacity: 1;
  visibility: visible;
}

.scroll-to-top-button:hover {
  background-color: #555;
}

.scroll-to-top-button:focus {
  outline: none;
}

.scroll-to-top-button::before {
  font-weight: 900;
  font-size: 16px;
}

.icon-up {
  width: 30px;
  height: 30px;
}

