@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');


.Tech-Stacks {
    padding-top: 100px;
    width: 100%;
    margin-bottom: 80px;
}

.heading {
    text-align: center;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    font-size: 2rem;
}

.heading-div {
    width: fit-content;
    margin: auto;
}


.heading-textbtn {
    color: black;
    font-family: inherit;
    font-weight: 800;

    margin: auto;
    position: relative;
    border: none;
    background: none;
    transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
    transition-duration: 400ms;
    transition-property: color;

}

.heading-textbtn:focus,
.heading-textbtn:hover {
    color: black;
}

.heading-textbtn:focus:after,
.heading-textbtn:hover:after {
    width: 100%;
    left: 0%;
}

.heading-textbtn:after {
    content: "";
    pointer-events: none;
    bottom: -2px;
    left: 50%;
    position: absolute;
    width: 0%;
    height: 2px;
    background-color: black;
    transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
    transition-duration: 400ms;
    transition-property: width, left;
}


/*      */


.logosofstacks {
    width: 65%;
    margin: auto;
    margin-top: 70px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 30px;
    padding-left: 8rem;
}

.coin {
    font-size: 100px;
    width: 0.1em;
    height: 1em;
    /* background: linear-gradient(#f7df1e, #141001); */
    animation: rotate_4001510 7s infinite linear;
    transform-style: preserve-3d;
}

.coin .side,
.coin:before,
.coin:after {
    content: "";
    position: absolute;
    width: 1em;
    height: 1em;
    overflow: hidden;
    border-radius: 50%;
    right: -0.4em;
    text-align: center;
    line-height: 1;
    transform: rotateY(-90deg);
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.coin .tails,
.coin:after {
    left: -0.4em;
    transform: rotateY(90deg);
}

.coin:before,
.coin:after {
    /* background: linear-gradient(#f7df1e, #141001); */
    backface-visibility: hidden;
    transform: rotateY(90deg);
}

.coin:after {
    transform: rotateY(-90deg);
}

@keyframes rotate_4001510 {
    100% {
        transform: rotateY(360deg);
    }
}

.svg_back {
    transform: scaleX(-1);
}


