.footer-button {
    display: flex;
    width: 40%;
    margin: auto;

}

.footer-box {
    width: 35px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    transition: all .8s;
    cursor: pointer;
    position: relative;
    background: #0E4066;
    overflow: hidden;
}

.footer-box:before {
    content: "";
    position: absolute;
    top: 0;
    background: #0E4066;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(100%);
    transition: transform .4s;
}

.footer-box:nth-child(9)::before {
    transform: translateY(-100%);
    content: 'T';
}

.footer-box:nth-child(10)::before {
    content: 'H';
}

.footer-box:nth-child(11)::before {
    transform: translateY(-100%);
    content: 'A';
}

.footer-box:nth-child(12)::before {
    content: 'N';
}

.footer-box:nth-child(13)::before {
    transform: translateY(-100%);
    content: 'K';
}

.footer-box:nth-child(14)::before {
    content: ' ';
}

.footer-box:nth-child(15)::before {
    transform: translateY(-100%);
    content: 'Y';
}

.footer-box:nth-child(16)::before {
    content: 'O';
}

.footer-box:nth-child(17)::before {
    transform: translateY(-100%);
    content: 'U';
}

.footer-box:nth-child(18)::before {
    content: '💚';
}




.footer-button:hover .footer-box:before {
    transform: translateY(0);
}

.footer-div {
    width: 100%;
    background-color: #0E4066;
    margin-top: 30px;
}


@media screen and(min-width:740px) and (max-width:930px) {
    .footer-box {
        font-size: 0.7rem;
        width: 30px;
        height: 35px;
        font-weight: 500;
    }
}

@media screen and (max-width:740px) {
    .footer-button {

        width: 50%;


    }

    .footer-box {
        font-size: 0.6rem;
        width: 25px;
        height: 30px;
        font-weight: 500;
    }
}